/* --------------------------------------------------------------
# Header
-------------------------------------------------------------- */

.site-header {
  .navbar-collapse {
    @media (max-width: 991.98px) {
      margin-top: 20px;
    }

    @media (max-width: 991.98px) {
      background-color: transparent;
    }
  }

  .navbar {
    @media (min-width: 992px) {
      padding-top: 25px;
      padding-bottom: 25px;
    }

    @media (max-width: 991.98px) {
      padding-top: 20px;
      padding-bottom: 20px;
    }

    .navbar-nav {
      margin-left: auto !important;

      @media (max-width: 991.98px) {
        background-color: #dfd7c3;
        padding: 20px;
      }
    }

    .nav-item {
      color: #747474;
      font-size: 1.125rem;
      margin-right: 9px;
      margin-left: 9px;

      @media (max-width: 991.98px) {
        padding-bottom: 10px;
        padding-top: 10px;
      }

      &.active {
        color: #2a5631;
      }

      .nav-link {
        font-family: "Roboto", sans-serif;
        font-size: 1rem;
        line-height: 25px;
        color: #747474;
        font-weight: 500;

        &:not(:last-child) {
          margin-right: 0;
        }

        @media (min-width: 992px) {
          position: relative;
        }

        .active::before,
        &:hover::before {
          @media (min-width: 992px) {
            content: "";
            border-bottom: solid 2px #bc5e49;
            width: calc(100% - 15px);
            display: block;
            position: absolute;
            bottom: -5px;
            z-index: 1;
          }
        }
      }

      .dropdown-menu {
        font-size: 1rem;
        font-weight: 800px;
        padding: 8px 0;
        margin: 0;
        margin-top: 20px;
        background-color: #dfd7c3;
        color: #2a5631;
        border: solid 1px #d7cbb7;

        @media (max-width: 991.98px) {
          padding: 0;
          background-color: #dfd7c3;
        }

        .dropdown-item {
          color: #4c4c4c;
          padding: 7px 32px;
          font-weight: 500;

          &:hover {
            background-color: #000;
            color: #fff;
          }

          @media (max-width: 991.98px) {
            padding: 13px 20px;
          }

          &.active {
            @media (max-width: 991.98px) {
              color: #fff;
              background-color: #2a5631;
            }
          }
        }
      }
    }

    .nav-item-account {
      i {
        font-size: 1.191rem;
        padding-right: 5px;
      }
    }
  }

  .nav-modules {
    padding: 0;
    margin: 0;

    li {
      margin-left: 0;
    }

    .nav-item-search {
      padding-top: 15px;
      padding-bottom: 15px;

      &:not(:last-child) {
        margin-right: 1.5rem;
      }

      .navbar-search-bar {
        @media (max-width: 767.98px) {
          padding-top: 0.6666666667rem;
          padding-bottom: 0.6666666667rem;
        }

        input {
          border-width: 1px;
          border-radius: 0;
          padding: 30px 0 30px 30px;
          border-right: none;
          background: #fff;
          width: 100%;
          border-top-left-radius: 50px;
          border-bottom-left-radius: 50px;

          @media (min-width: 992px) {
            width: 320px;
            border-top: none;
            border-bottom: none;
          }
        }

        .btn {
          border-left: none;
          background: #fff;
          border-top-left-radius: 0;
          border-bottom-left-radius: 0;
          border-top-right-radius: 50px;
          border-bottom-right-radius: 50px;
          padding: 15px 30px 15px 0;
          font-size: 0.875rem;
        }
      }
    }

    .btn-link {
      font-size: 1.125rem;
      font-weight: bold;
      font-family: "Playfair Display", sans-serif;
      color: #c8b6a1;
    }

    .nav-item-social-nav {
      @media (max-width: 767.98px) {
        padding-top: 0.6666666667rem;
        padding-bottom: 0.6666666667rem;
      }

      &:not(:last-child) {
        margin-right: 1.2rem;
        padding-right: 1.2rem;
        border-right: solid 1px #000;
      }

      a {
        &:not(:last-child) {
          margin-right: 1rem;

          @media (max-width: 767.98px) {
            margin-right: 1.3rem;
          }
        }
      }
    }
  }

  .navbar-toggler {
    border-radius: 0;
    background-color: #fff;
  }
}

.btn-outline-black-header {
  border: solid;
  border-width: 2px;
  padding-right: 15px;
  padding-left: 15px;
  padding-top: 10px;
  padding-bottom: 10px;
}

.nav-pills {
  .nav-link {
    padding: 0.75rem 2rem;
    border-radius: 2rem;

    &.active {
      color: #fff;
    }
  }
}

.header {
  &.ml-negative {
    @media (min-width: 992px) {
      margin-left: -40px;
    }
  }

  &.mr-negative {
    @media (min-width: 992px) {
      margin-right: -40px;
    }
  }
}

.mt-negative {
  @media (min-width: 992px) {
    margin-top: -40px;
  }
}

.border-image {
  @media (min-width: 768px) {
    border: solid;
    border-width: 20px;
    margin-top: -380px;
    margin-left: 210px;
  }
}

.header-margin {
  @media (min-width: 992px) {
    margin-bottom: -145px;
  }
}

.navbar-icon-size {
  font-size: 17px;
  line-height: 22px;
  font-weight: 400;
  color: #2a5631;
}

.nav-tabs {
  .nav-link {
    border: 2px solid transparent;
    border-top: 0;
    border-left: 0;
    border-right: 0;
    font-size: 20px;
  }
}



